import './InstaBox.scss';

function InstaBox () {
	return (
		<section className='insta-box'>
			<div className='insta-box__wrapper'>
				<a  href='#' className='insta-box__title'>Мы в Instagram</a>
				<img className='insta-box__picture'></img>
			</div>
		</section>
	)
}

export default InstaBox;